.title {
  font-family: var(-main-font);
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 0.8;

  color: var(--title-color);

  @media screen and (min-width: 768px) {
    font-size: 36px;
    line-height: 1.22;
  }

  @media screen and (min-width: 1200px) {
    font-size: 48px;
    line-height: 1.23;
  }
}

.title_second {
  font-family: var(-main-font);
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.22;

  color: var(--title-color);

  @media screen and (min-width: 768px) {
    font-size: 24px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 35px;
  }
}

.btn {
  height: 56px;
  width: 280px;
  font-family: var(--main-font);
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.5;
  color: var(--title-color);

  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  background: #f5f5fa;
  box-shadow: -10px -10px 20px #ffffff, 10px 10px 20px rgba(72, 121, 150, 0.1),
    5px 5px 10px rgba(72, 121, 150, 0.25), -5px -5px 10px #ffffff;
  border-radius: 30.5px;
}

.text {
  font-family: var(--secondary-font);
  color: var(--text-color);
  font-weight: 300;
  font-size: 16px;
  line-height: 1.2;

  @media screen and (min-width: 1200px) {
    font-size: 18px;
  }
}
